}

fieldset,
iframe,
img {
	border: 0;
}

caption,
th {
	text-align: left;
}

table {
	border-spacing: 0;
	border-collapse: collapse;
}

article,
aside,
footer,
header,
nav,
main,
section,
summary,
details,
hgroup,
figure,
figcaption {
	display: block;
}

audio,
canvas,
video,
progress {
	display: inline-block;
	vertical-align: baseline;
}

button {
	font: inherit;
	line-height: inherit;
	overflow: visible;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	cursor: pointer;
	vertical-align: inherit;
	color: inherit;
	border: 0;
	background: none;
}

button:disabled {
	cursor: default;
}

a {
	cursor: pointer;
	text-decoration: none;
	color: inherit;
}

a:active,
a:hover {
	outline: 0;
}

h1 {
