}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -.5em;
}

button,
input,
select,
textarea {
	font-family: inherit;
	/* 1 */
	font-size: 100%;
	/* 2 */
	margin: 0;
	/* 3 */
}

input:focus,
select:focus,
textarea:focus,
button:focus,
button:active {
	outline: none;
}

table {
	border-spacing: 0;
	border-collapse: collapse;
}

.clearfix:after {
	display: block;
	visibility: hidden;
	clear: both;
	content: ' ';
}

.clearfix {
	zoom: 1;
}

