.btn {
	line-height: 54px;
	height: 55px;
	padding: 0 10px;
}

.btn,
.btn[href] {
	font-family: 'moderat', sans-serif;
	font-size: 14px;
	font-weight: bold;
	display: inline-block;
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
	text-align: center;
	white-space: nowrap;
	letter-spacing: .25em;
	text-transform: uppercase;
	color: #d1434a;
	border: 1px solid #d1434a;
}

.btn:hover,
.btn[href]:hover {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
	background-color: #ffe5e6;
}

.btn--red,
.btn--red[href] {
	color: #fff;
	border-color: #d1434a;
	background-color: #d1434a;
}

.btn--red:hover,
.btn--red[href]:hover {
	color: #fff;
	border-color: #f16f75;
	background-color: #f16f75;
}

.btn--m {
	line-height: 40px;
	height: 40px;
}

.btn--s {
	font-size: 12px;
	line-height: 35px;
	height: 35px;
	letter-spacing: .1em;
}

.btn-line2 {
	line-height: 1em;
	vertical-align: top;
	white-space: normal;
}

.up-down-btn {
	color: #ee7a23;
}

.i-up-down {
	position: relative;
	z-index: 2;
	display: inline-block;
	width: 14px;
	height: 6px;
	-webkit-transition: -webkit-transform .3s ease-out;
	transition: -webkit-transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
	-webkit-transform: translateY(2px);
	transform: translateY(2px);
	vertical-align: middle;
}

.i-up-down i {
	position: absolute;
	top: 0;
	width: 8px;
	height: 1px;
	background: #ee7a23;
}

.i-up-down .i-up-down-l {
	left: 0;
	-webkit-transition: -webkit-transform .3s ease-out;
	transition: -webkit-transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
	-webkit-transform: rotate(40deg);
	transform: rotate(40deg);
}

.i-up-down .i-up-down-r {
	right: 0;
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
	-webkit-transform: rotate(-40deg);
	transform: rotate(-40deg);
}

.select-self {
	position: relative;
	display: inline-block;
	cursor: pointer;
}

.select-self-area {
	line-height: 30px;
	width: auto;
	min-width: 50px;
	max-width: 240px;
	height: 30px;
	border-radius: 3px;
	background: #f0f0f0;
}

.select-self-area .select-ipt {
	font-size: 16px;
	display: block;
	overflow: hidden;
	min-width: 56px;
	height: 100%;
	padding: 0 25px 0 10px;
	text-align: left;
	color: #605f5f;
	border: 0;
}

.select-self-area .select-btn {
	position: absolute;
	top: 0;
	right: 0;
	width: 18px;
	height: 100%;
	text-align: center;
	border: 1px solid #ccc;
	border-radius: 3px;
	background: #fff;
}

.select-self-area .select-btn .i-up-down {
	top: 0;
	left: 0;
	width: 8px;
}

.select-self-area .select-btn .i-up-down i {
	width: 5px;
	height: 2px;
	background-color: #605f5f;
}

.select-self-open {
	border-radius: 3px 3px 0 0;
}

.select-self-open .select-options {
	display: block;
}

.select-self-open .i-up-down .i-up-down-l {
	left: 0;
	-webkit-transition: -webkit-transform .3s ease-out;
	transition: -webkit-transform .3s ease-out;
	transition: transform .3s ease-out;
	transition: transform .3s ease-out, -webkit-transform .3s ease-out;
	-webkit-transform: rotate(-40deg);
	transform: rotate(-40deg);
}

.select-self-open .i-up-down .i-up-down-r {
	right: 0;
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
	-webkit-transform: rotate(40deg);
	transform: rotate(40deg);
}

.select-self-open .select-options {
	z-index: 102;
}

.select-self-open.select-up .select-options {
	top: auto;
	bottom: 30px;
}

.up-down-btn.open {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
}

.up-down-btn.open .i-up-down-l {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
	-webkit-transform: rotate(-40deg);
	transform: rotate(-40deg);
}

.up-down-btn.open .i-up-down-r {
	-webkit-transition: all .3s ease-out;
	transition: all .3s ease-out;
	-webkit-transform: rotate(40deg);
	transform: rotate(40deg);
}

